import { create } from "zustand";

// ID16ID
function generateRandomString(length) {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    return Array.from(crypto.getRandomValues(new Uint8Array(length)))
    .map(byte => chars[byte % chars.length])
    .join('');
}

const useUserStore = create((set,get) => ({
    users: [
        {id: 'Kho5GIoLa3irqGRt', name: '', email: 'admin@163.com'},
        {id: 'BvwQzKClL50gI2sA', name: '', email: 'user@163.com'},
        {id: 'BvwQzKClL123I2sA', name: '', email: 'guest@163.com'},
    ],
      // 
      addUser: (newUser) =>{
        set(state => ({ users: [...state.users, {...newUser, id: generateRandomString(16) }] }) )
    },
    // 
    updateUser: (upUser) =>{
        set(state => ({
            users: state.users.map(user => user.id=== upUser.id ? upUser : user)
        }) )
    },
    // 
    deleteUser: (cid) =>{
        set(state => ({ users: state.users.filter(v=>v.id!==cid) }) )
    },
    getAllUsers: () =>{
        return get().users;
    },
}))

export default useUserStore;