import { create } from 'zustand';

const useStore = create((set) => ({
  user: null, // 
  setUser: (user) => set({ user }),

  isMenuOpen: true, // /
  toggleMenu: () => set((state) => ({ isMenuOpen: !state.isMenuOpen })),

  sharedData: null, // 
  setSharedData: (data) => set({ sharedData: data }),
}));

export default useStore;
